<template>
  <div class="page-content">
    <div class="page-content-style">
      <div class="bulkl">
          <div class="title">
            导入人员
          </div>
          <div class="step">
              <el-steps :active="active" simple class="el-steps--simple">
              <el-step title="数据导入"></el-step>
              <el-step title="数据浏览"></el-step>
              <el-step title="完成"></el-step>
              </el-steps>            
          </div>
          <div class="content-boxs">
              <div class="content-box box1" v-if="active==1">
                  <div class="up-alert">
                      <p>提示：</p>
                      <p>
                        第一步：下载导入数据Exlcel模板;
                          <el-dropdown>
                            <span class="el-dropdown-link">
                              选择下载模板<i class="el-icon-arrow-down el-icon--right"></i>
                            </span>
                            <el-dropdown-menu slot="dropdown">
                              <el-dropdown-item v-for="item in peopleType" :key="item.categoryName">
                                <a :href="netAPI.exportExcel+'?serviceCode=1010&attribute='+item.categoryNo">【{{item.categoryName}}】模板</a>
                              </el-dropdown-item>
                            </el-dropdown-menu>
                          </el-dropdown>
                      </p>
                      <p>第二步：按模板要求填写导入数据，（标红为必填项）;</p>
                      <p>第三步：将文件压缩为.zip格式</p>
                  </div>
                  <div class="selectType">
                      选择导入的类型：
                      <el-select v-model="peopleTypeCurrent" clearable placeholder="请选择">
                        <el-option
                          v-for="item in peopleType"
                          :key="item.value"
                          :label="item.categoryName"
                          :value="item.categoryNo">
                        </el-option>
                      </el-select>
                  </div>
                  <div class="uploadChunk">
                    <div>
                      <div class="title">
                        人员信息文件
                      </div>
                      <div class="upload-demo">
                        <el-upload
                          ref="uploadInfo"
                          action=""
                          accept=".xls,.xlsx"
                          :on-remove="uploadInfoRemove"
                          :on-change="uploadInfoChange"
                          :http-request="parseXlsORx"
                          :multiple="false"
                          :limit='1'
                          :auto-upload="false">
                            <el-tooltip class="item" effect="dark" content="仅支持：.xls,.xlsx" placement="right">
                              <el-button size="small" >添加文件</el-button>
                            </el-tooltip>                          
                          </el-upload>                       
                      </div>
                    </div>
                    <div>
                      <div class="title">
                        人员照片文件
                      </div>
                      <div class="upload-demo">
                        <el-upload
                          ref="uploadImg"
                          action=""
                          :http-request="upZip"
                          :multiple="false"
                          accept=".zip"
                          :limit='1'
                          :auto-upload="false">
                          <el-tooltip class="item" effect="dark" content="照片压缩包 仅支持：.zip" placement="right">
                            <el-button size="small" >添加文件</el-button>
                          </el-tooltip>
                          </el-upload>                       
                      </div>
                    </div>
                    <div
                      v-loading="true"
                      element-loading-text="暂不支持"
                      element-loading-spinner="el-icon-warning">
                      <div class="title">
                        人员指纹文件
                      </div>
                      <div class="upload-demo">
                        <el-upload
                          ref="fingerprint"
                          action=""
                          :limit='1'
                          :http-request="parseXlsORx"
                          :multiple="false"
                          :auto-upload="false">
                            <el-button size="small" >添加文件</el-button>
                        </el-upload>                       
                      </div>
                    </div>                                        
                  </div>
              </div>
              <div class="content-box" v-if="active==2">
              <template>
                <el-table
                  :data="tableData"
                  style="width: 100%">
                  <el-table-column
                    v-for="item in tableDataLabel"
                    :key="item.name"
                    :prop="item.property"
                    :label="item.name"
                    >
                  </el-table-column>
                </el-table>
              </template>
              </div>
              <div class="content-box" v-if="active==3">
                <div>导入列表：
                  <label class="succeedResponse">导入成功{{tableData.length-errResponse.length}}条</label> 
                  <label class="warnResponse">导入警告{{warnResponse.length}}条</label>
                  <label class="errResponse">导入失败{{errResponse.length}}条</label>
                </div>
                <div>
                  <el-table
                    :data="tableData"
                    id='out-table'
                    style="width: 100%">
                    <el-table-column
                      v-for="item in tableDataLabel"
                      :key="item.name"
                      :prop="item.property"
                      :label="item.name"
                      >
                    </el-table-column>
                  </el-table>
                </div>
              </div>
          </div>
          <div class="button">
              <el-button type="primary" size="mini" @click="$router.push('/people_manage')" v-if="active==1">返回人员管理</el-button>
              <el-button type="primary" size="mini" @click="stepsNext" v-if="active!=3" :loading="buttonLoading">{{buttonLoading?'上传中':'下一步'}}</el-button>
              <el-button type="primary" size="mini"  @click="stepsBack" v-if="active!=3&&active!=1">重新上传</el-button>
              <el-button  size="mini" @click="exportExcel" v-if="active==3" >导出失败名单</el-button>
              <el-button type="primary" size="mini"  @click="stepsBack" v-if="active==3">完成</el-button>         
          </div>
      </div> 
    </div>
</div>    
</template>
<script>
import APICONFIG from "../../../../config/api.js";
import XLSX from "xlsx";
import FileSaver from "file-saver";
export default {
  name: "asd",
  data () {
    return {
      active: 1,
      title: "导入人员",
      uploadInfoFile: 0,
      excelOBj: "", //excel数据
      netAPI: null,
      peopleType: [], //人员类型
      peopleTypeCurrent: null, //当前选中的类型对象
      tableDataLabel: [], //表格头
      tableData: [], //表格数据
      cacheFile: null, //压缩包
      errResponse: [], //错误的响应
      warnResponse: [], //警告的响应
      buttonLoading: false, //上传时候的按钮加载
      errorDataLabel: []
    };
  },
  created () {
    this.netAPI = APICONFIG().get("bulkImport");
    this.getPeopleType();
  },
  methods: {
    stepsNext () {
      switch (this.active) {
        case 1:
          if (!this.uploadInfoFile) {
            this.$message.warning("人员信息文件为必要文件!");
            return;
          }
          if (!this.peopleTypeCurrent) {
            this.$message.warning("请选择人员类型!");
            return;
          }
          this.$refs.uploadInfo.submit();
          this.$refs.uploadImg.submit();
          break;
        case 2:
          this.upload();
          break;
        case 3:
          break;
        default:
          break;
      }
    },
    stepsBack () {
      if (this.active > 1) {
        //清空选择
        this.tableDataLabel = [];
        this.tableData = [];
        this.peopleTypeCurrent = null;
        this.active = 1;
        this.cacheFile = null;
        this.errResponse = []; //错误的响应
        this.warnResponse = []; //警告的响应
        this.buttonLoading = false; //复原按钮
        this.errorDataLabel = [];
      } else {
      }
    },
    parseXlsORx (content) {
      //取得文件
      let file = content.file;
      //fileReader
      let reader = new FileReader();
      //开始读取指定的Blob中的内容
      reader.readAsBinaryString(file);
      //读取完成
      reader.onload = e => {
        let data = e.target.result;
        let wb = XLSX.read(data, {
          type: "binary"
        });
        this.excelOBj = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]);
        console.log("excel:", this.excelOBj);
        this.getPeopleAttribute();
        //xl 就是解析出来的文件
      };
    },
    upZip (content) {
      this.cacheFile = content.file;
    },
    uploadInfoChange () {
      //判断是否发送改变
      this.uploadInfoFile++;
    },
    uploadInfoRemove () {
      this.uploadInfoFile--;
    },
    getPeopleType () {
      //获取类型
      this.$http.get(this.netAPI.findPeopleType).then(
        res => {
          if (res.body.status == 200) {
            this.peopleType = res.body.data;
          } else {
            this.$message.warning(JSON.stringify(res.body.message));
          }
        },
        err => {
          this.$message.warning(JSON.stringify(err));
        }
      );
    },
    getPeopleAttribute () {
      //更具选中的代码来获取类型
      this.$http
        .get(this.netAPI.findPeopleAttribute, {
          params: { categoryNo: this.peopleTypeCurrent }
        })
        .then(
          res => {
            if (res.body.status == 200) {
              let data = res.body.data;
              data.push({
                categoryDescription: "照片",
                categoryName: "ext_30"
              })
              if (this.parseTablehead(data)) {
                this.parseTableData();
                this.active++;
              } else {
                this.$message.warning("选中的类型与添加的文件不匹配！");
                this.stepsBack();
              }
            } else {
              this.$message.warning(JSON.stringify(res.body.message));
            }
          },
          err => {
            this.$message.warning(JSON.stringify(err));
          }
        );
    },
    parseTablehead (data) {
      //吧execl对应映射解析出 表头
      let excelZERO = this.excelOBj.shift();
      for (let i in excelZERO) {
        for (let item of data) {

          if (item.categoryDescription == excelZERO[i]) {
            this.tableDataLabel.push({
              name: item.categoryDescription,
              property: item.categoryName,
              index: i
            });
          }
        }
      }

      // this.tableDataLabel.push({
      //   name: '图片名字',
      //   property: 'ext_30',
      //   index: excelZERO.length
      // });
      //判断是否对应 通过判断 返回的映射关系和excel的第一条长是否匹配
      if (this.tableDataLabel.length < data.length) {
        return false;
      }
      return true;
    },
    parseTableData () {
      //通过数据解析出
      for (let item of this.excelOBj) {
        let obj = {};
        for (let table of this.tableDataLabel) {
          obj[table.property] = item[table.index];
        }
        this.tableData.push(obj);
      }
    },
    upload () {
      //上传状态
      this.buttonLoading = true;
      let form = new FormData();
      form.append("personsJson", JSON.stringify(this.tableData));
      form.append("attribute", this.peopleTypeCurrent);
      if (this.cacheFile) {
        form.append("attachmentFile", this.cacheFile);
      }

      this.$http.post(this.netAPI.importExcel, form).then(
        res => {
          if (res.body.status == 200) {
            //errorType warn error
            let mes = JSON.parse(res.body.message);
            for (let item of mes) {
              if (item.errorType == "warn") {
                this.warnResponse.push(item);
              } else if (item.errorType == "error") {
                this.errResponse.push(item);
              }
            }
            this.tableDataLabel.push({
              name: "错误原因",
              property: "error"
            });
            this.tableData = this.errResponse.concat(this.warnResponse);
            this.buttonLoading = false;
            this.active++;
          } else {
            this.$message.warning(
              "上传失败:" + JSON.stringify(res.body.message)
            );
          }
        },
        err => {
          this.$message.warning("上传失败:" + JSON.stringify(err));
        }
      );
    },
    exportExcel () {
      //导出
      let wb = XLSX.utils.table_to_book(document.querySelector("#out-table"));
      let wbout = XLSX.write(wb, {
        bookType: "xlsx",
        bookSST: true,
        type: "array"
      });
      try {
        FileSaver.saveAs(
          new Blob([wbout], { type: "application/octet-stream" }),
          "上传错误.xlsx"
        );
      } catch (e) {
        if (typeof console !== "undefined") console.log(e, wbout);
      }
      return wbout;
    }
  }
};
</script>

<style scoped>
.content-box {
  margin: 10px 0;
}
.step {
  margin: 10px 0;
}
.page-content {
  height: 100%;
  vertical-align: top;
  position: relative;
}
.box1 {
  width: 100%;
}
.button {
  border-top: 1px solid #e6eaf2;
  text-align: right;
  padding: 20px;
}
.succeedResponse {
  color: #67c23a;
}
.warnResponse {
  color: #e6a23c;
}
.errResponse {
  color: #f56c6c;
}
.page-content-style {
  position: absolute;
  top: 9px;
  right: 0;
  left: 0;
  margin: auto;
  background: #ffffff;
  width: 98%;
  border-radius: 5px;
  padding: 10px;
}
.up-alert {
  background: #f5f7fa;
  display: block;
  padding: 5px 10px;
  position: relative;
  margin: 5px 0px;
}
.up-alert::before {
  content: " ";
  display: inline-block;
  background: #f56c6c;
  position: absolute;
  width: 7px;
  height: 100%;
  top: 0px;
  left: 0px;
}
.selectType {
  margin-left: 10px;
}
.uploadChunk {
  margin-top: 6px;
}
.uploadChunk .upload-demo {
  text-align: left;
  padding: 10px 15px;
}
.uploadChunk .title {
  padding: 10px 15px;
}
.uploadChunk > div {
  vertical-align: top;
  width: 300px;
  height: 135px;
  border: 1px dashed #79bbff;
  display: inline-block;
  margin-left: 10px;
}
.up-alert p {
  font-size: 14px;
  color: #606266;
  margin-top: 7px;
  color: #f56c6c;
  display: block;
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0em;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
  padding: 5px 10px;
}

.bulkl > .title {
  padding: 10px;
  font-size: 20px;
  border-bottom: 1px solid #e6eaf2;
}
.el-steps--simple {
  padding: 7px 8%;
}
</style>



